.log-in{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 21;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    @include transition-ease-faster;
    &.open{
        opacity: 1;
        visibility: visible;      
    }
    .popup-box{
        position: relative;
    }
    .exit-form{
        @include flexCenter(row);
        position: absolute;
        top: 0px;
        left: 50%;
        transform: translateX(-50%);
        border-bottom-right-radius: 20px;
        border-bottom-left-radius: 20px;
        width: 40px;
        height: 40px;
        background: #4A3600;
        text-align: center;
        touch-action: manipulation;
        display: none;
        i{
            color: $dark-purple;
            font-size: 22px;
            position: relative;
            top: 6px;
        }
    }
    .logo{
        margin-bottom: 50px;
    }
    input, button{
        font-family: 'Crimson Text', serif;
        width: 100%;
        margin: auto;
        border: none;
        padding: 0 20px;
        margin-bottom: 20px;
        color: #000;
        border-radius: 3px;
        height: 50px;
        font-size: 14px;
    }
    button{
        font-size: 15px;
        border-radius: 0px;
        background-color: $orange;
        color: white;
        margin-bottom: 0px;
    }
    a{
        margin: 33px 0px 13px 0px;
    }
    .popup-box{
        width: 558px;
        padding: 60px 40px;
        border-radius: 5px;
        background-color: #4B38D2;
    }
    .title{
        font-size: 24px;
        color: #fff;
        margin-bottom: 30px;
        text-align: left;
    }
}

@include max-mobile{
    .log-in{
        height: 100%;
        overflow-y: scroll !important;
        .exit-form{
            display: block;
        }
        .popup-box{
            width: 420px;
            padding: 45px 30px;
        }
    }
}

@include max-smobile{
    .log-in{
        .popup-box{
            width: 320px;
        }
    }
}