.large-banner-pages {
    position: relative;
    background-image: url("../assets/images/bradcam.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-bottom: 120px;
    &::before {
        position: absolute;
        content: "";
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: #2e004b;
        opacity: 0.6;
    }
    .info {
        position: relative;
        color: white;
        text-align: center;
        padding: 269px 0 159px 0;
        h3 {
            font-size: 60px;
            font-weight: 400;
        }
    }
}

@include max-tablet {
    .large-banner-pages {
        margin-bottom: 120px;
        .info {
            padding: 150px 0px;
            h3 {
                font-size: 40px;
            }
        }
    }
}

@include max-mobile {
    .large-banner-pages {
        margin-bottom: 30px;
        .info {
            h3 {
                font-size: 30px;
            }
        }
    }
}
