.about {
    margin-bottom: 270px;
    .large-banner-pages {
        margin-bottom: 200px;
    }
    .title {
        font-size: $font-size-other;
        line-height: $line-height-other;
        font-weight: 400;
        color: $purple;
        margin-bottom: 18px;
    }
    .desc {
        color: $grey;
        margin-bottom: 42px;
    }
    .list-courses {
        @include flexCenter(row);
        justify-content: space-around;
    }
    .course {
        position: absolute;
        @include flexCenter(column);
        color: white;
        .number {
            font-size: 60px;
            line-height: 70px;
        }
        .text {
            font-size: 18px;
        }
        &.orange {
            width: 270px;
            height: 270px;
            content: "";
            top: -5px;
            left: 10%;
            z-index: 1;
            border-radius: 50%;
            background-color: rgba(253, 142, 94, 0.7);
            z-index: 3;
        }
        &.blue {
            width: 310px;
            height: 310px;
            content: "";
            right: 0;
            top: -60px;
            z-index: 1;
            border-radius: 50%;
            background-color: rgba(93, 178, 255, 0.7);
        }
        &.cyan {
            width: 310px;
            height: 310px;
            content: "";
            left: 27%;
            top: 50%;
            z-index: 2;
            border-radius: 50%;
            background-color: rgba(4, 210, 200, 0.7);
        }
    }
}
@include max-desktop {
    .about {
        .course {
            &.orange {
                left: 0;
            }
        }
    }
}

@include max-tablet {
    .about {
        .left {
            margin-bottom: 30px;
        }
        .title {
            font-size: 25px;
            line-height: 32px;
            font-weight: 400;
            color: $purple;
            margin-bottom: 18px;
        }
        .desc {
            color: $grey;
            margin-bottom: 42px;
        }
        .course {
            position: relative;
            @include flexCenter(column);
            .number {
                font-size: 35px;
                line-height: 39px;
            }
            &.orange {
                width: 200px;
                height: 200px;
                content: "";
                top: initial;
                left: initial;
                z-index: 1;
                border-radius: 50%;
                background-color: rgba(253, 142, 94, 0.7);
                z-index: 3;
            }
            &.blue {
                width: 200px;
                height: 200px;
                content: "";
                right: initial;
                top: initial;
                z-index: 1;
                border-radius: 50%;
                background-color: rgba(93, 178, 255, 0.7);
            }
            &.cyan {
                width: 200px;
                height: 200px;
                content: "";
                left: initial;
                top: initial;
                z-index: 2;
                border-radius: 50%;
                background-color: rgba(4, 210, 200, 0.7);
            }
        }
    }
}

@include max-mobile {
    .about {
        margin-bottom: 100px;
        .list-courses {
            @include flexCenter(column);
        }
        .course {
            & {
                margin-bottom: 30px;
            }
            .number {
                font-size: 60px;
                line-height: 70px;
            }
            &.orange {
                width: 290px;
                height: 290px;
            }
            &.blue {
                width: 290px;
                height: 290px;
            }
            &.cyan {
                width: 290px;
                height: 290px;
            }
        }
    }
}
