.testimonial {
    position: relative;
    background-image: url("../assets/images/testimonial/testimonial.webp");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    margin-bottom: 200px;
    cursor: pointer;
    &:before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        background-color: rgba(0, 0, 0, 0.5);
    }
    .item {
        outline: none;
        position: relative;
        text-align: center;
        color: white;
        padding: 160px 0px;
        .image {
            text-align: center;
            margin-bottom: 26px;
            img {
                margin: auto;
            }
        }
        p {
            width: 100%;
            max-width: 555px;
            font-family: "Poppins", sans-serif;
            font-size: 24px;
            line-height: 34px;
            margin: 0px auto 22px;
        }
    }
}

@include max-mobile {
    .testimonial {
        margin-bottom: 50px;
        .item {
            p {
                font-size: 18px;
            }
        }
    }
}
