.contact-area {
    /* */
    margin-bottom: 130px;
    .title {
        font-size: 27px;
        font-weight: 600;
        margin-bottom: 20px;
    }
    .list-items {
        .item {
            @include flexCenter(row);
            align-items: stretch;
            justify-content: stretch;
            margin-bottom: 25px;
        }
        .icon {
            text-align: center;
            width: 27px;
            color: #8f9195;
            font-size: 27px;
            margin-right: 20px;
        }
        .info {
            h4 {
                font-size: 16px;
            }
            span {
                color: #8a8a8a;
            }
        }
    }

    form {
        .btn {
            padding: 22px 44px;
            height: 59px;
        }
        input,
        textarea {
            margin-bottom: 30px;
        }
        input {
            height: 48px;
        }
        textarea {
            height: 100%;
            max-height: 185px;
        }
        .btn {
            width: auto;
        }
    }

    .map {
        /* Responsive for bitmap */
        .iframe-rwd {
            position: relative;
            padding-bottom: 540px;
            padding-top: 10px;
            height: 0;
            overflow: hidden;
            width: 100%;
        }
        .iframe-rwd iframe,
        .iframe-rwd object,
        .iframe-rwd embed {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}

@include max-tablet {
    .contact-area {
        margin-bottom: 30px;
        form {
            margin-bottom: 30px;
        }
    }
}
