.courses-details-info{
    margin-bottom: 200px;
    .title{
        font-size: 36px;
        font-weight: 400;
        margin-bottom: 30px;
    }
    .left{
        .desc{
            color: $medium-grey;
        }
        .objective{
            margin-bottom: 52px;
        }
        .item{
            border: 1px solid rgba(0, 0, 0, 0.125);
            padding: 14px 0 18px 14px;
            margin-bottom: 30px;
            .question{
                cursor: pointer;
                padding: 6px 12px 0px 12px;
                i{
                    margin-right: 15px;
                    font-size: 24px;
                    color: $light-green;
                }
                span{
                    font-family: "Poppins", sans-serif;
                    font-size: 22px;
                    font-weight: 400;
                    color: $black;
                }
            }
            .desc{
                font-size: 16px;
                line-height: 25px;
                padding: 22px 10px 0px 62px;
                display: none;
            }
        }
    }
    .right{
        a{
            color: $black;
        }
        .video{
            position: relative;
            margin-top: -400px;
            margin-bottom: 40px;
        }
        .play-btn{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            height: 90px;
            width: 90px;
            border-radius: 50%;
            background-color: $dark-orange;
            @include flexCenter(row);
            &::before{
                position: absolute;
                margin-left: 2px;
                content: '';
                height: 16px;
                border: 14px solid white;
                border-top: 8px solid transparent;
                border-bottom: 8px solid transparent;
                border-right: 2px solid transparent;
            }
        }
        .socialities{
            @include flexCenter(row);
            justify-content: stretch;
            .society{
                margin-right: 17px;
                a{
                    color: #D1D1D1;
                }
            }
        }
        .author-info{
            padding: 30px;
            border: 1px solid #E9E5EC;
            margin-bottom: 40px;
        }
        .author-info__header{
            @include flexCenter(row);
            justify-content: stretch;
            margin-bottom: 14px;
            .thumb{
                margin-right: 15px;
            }
            h3{
                font-size: 18px;
                font-weight: 400;
                margin-bottom: 4px;
            }
            span{
                font-size: 14px;
                color: $medium-grey;
            }
        }
        .desc{
            font-size: $font-size-secondary;
            color: $medium-grey;
            margin-bottom: 20px;
        }
        /* */
        .btn{
            width: 100%;
            color: white;
            margin-bottom: 40px;
            &:hover{
                color: $cyan;
            }
        }
        /* */
        .feedback{
            padding: 35px;
            border: 1px solid #E8E8E8;
            h3{
                font-size: 18px;
                color: $black;
                font-weight: 400;
                margin-bottom: 27px;
            }
            p{
                font-family: "Poppins", sans-serif;
                font-size: 14px;
            }
        }
        .rating{
            margin-bottom: 15px;
            font-size: 20px;
            color: $medium-orange;
        }
        form{
            textarea{
                font-family: "Poppins", sans-serif;
                width: 100%;
                border: 1px solid #E8E8E8;
                padding: 15px;
                margin-bottom: 30px;
                overflow: auto;
                resize: vertical;
            }
            .btn{
                margin-bottom: 0px;
            }
        }
    }
}

@include max-tablet{
    .courses-details-info{
        .left{
            .item{
                .question{
                    i{
                        font-size: 13px;
                    }
                    span{
                        font-size: 13px;
                    }
                }
                .desc{
                    font-size: 15px;
                    padding: 22px 10px 0px 30px;
                }
            }
        }    
        .right{
            img{
                width: 100%;
            }
            .video{
                margin-top: 0px;
            }
        }
    }
}

@include max-mobile{
    .courses-details-info{
        margin-bottom: 50px;
        .left{
            .item{
                padding-top: 15px;
                padding-bottom: 16px;
                .question{
                    padding-top: 0px;
                    i{
                        font-size: 13px;
                    }
                    span{
                        font-size: 13px;
                    }
                }
                .desc{
                    font-size: 15px;
                    padding: 22px 10px 0px 30px;
                }
            }
        }
        .right{
            .feedback{
                padding: 25px;
            }
        }    
    }
}

@include max-smobile{
    .courses-details-info{
        .left{
            .item{
                .desc{
                    padding: 22px 10px 0px 14px;
                }
            }
        }    
    }
}