.my-team{
    margin-bottom: 130px;
    .item{
        margin-bottom: 20px;
    }
    .image-socialities{
        position: relative;
        overflow: hidden;
        margin-bottom: 25px;
        img{
            width: 100%;
            object-fit: cover;
            transform: scale(1);
            @include transition-ease-faster;
        } 
        &::before{
            position: absolute;
            content: '';
            bottom: 0;
            width: 100%;
            height: 100%;
            background-image: url('../assets/images/our-team-member/overlay.png');
            background-repeat: no-repeat;
            background-size: cover;
            background-position: bottom;
            opacity: 0;
            visibility: hidden;
            z-index: 1;
            @include transition-ease-faster;
        }   
    }

    .item:hover .image-socialities::before,  .item:hover .socialities{
        opacity: 1;
        visibility: visible;
    }

    .item:hover .socialities{
        transform: translate(-50%, 0%);
    }
    .item:hover{
        img{
            transform: scale(1.1);
        }
    }

    .socialities{
        @include flexCenter(row);
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, -75%);
        padding: 30px 0px;
        opacity: 0;
        visibility: hidden;
        z-index: 2;
        @include transition-ease;
        a{
            color: #ddd;
        }
        .society{
            font-size: 20px;
            margin: 0px 10px;
        }
    }

    .info{
        text-align: center;
        h3{
            font-family: "Poppins", sans-serif;
            font-size: 24px;
            font-weight: 400;
            color: $black;
        }
        p{
            font-size: 14px;
            color: #919191;

        }
        
    }
}

@include max-mobile{
    .my-team{
        margin-bottom: 60px;
    }
}