.popular-courses {
    margin-bottom: 100px;
    .nav {
        @include flexCenter(row);
        justify-content: space-between;
        margin-bottom: 100px;
        border-bottom: 1px solid #e8e8e8;
    }
    .nav-item {
        line-height: 84px;
        border: none;
    }
    .nav-link {
        font-family: "Poppins", sans-serif;
        font-weight: 500;
        font-size: $font-size-secondary;
        position: relative;
        padding: 0px;
        height: 100%;
        color: $grey;
        border: none;
        &::before {
            position: absolute;
            content: "";
            bottom: 0;
            left: 0;
            right: 0;
            height: 3px;
            background: transparent;
            @include transition-ease-faster;
        }
        &.active {
            border: none;
            color: $black;
            &::before {
                position: absolute;
                content: "";
                bottom: 0;
                left: 0;
                right: 0;
                height: 3px;
                background: $cyan;
            }
        }
    }
    .tab-content {
        margin-bottom: 10px;
        .item {
            margin-bottom: 50px;
            box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
            overflow: hidden;
        }
        .info {
            font-family: "Poppins", sans-serif;
            padding: 30px 30px 15px 30px;
            .title {
                font-size: 14px;
                margin-bottom: 11px;
                color: $dark-grey;
            }
            .desc {
                font-size: 18px;
                font-weight: 500;
                margin-bottom: 30px;
                line-height: 28px;
                @include transition-ease-faster;
                a {
                    color: $black;
                }
            }
            .top {
                border-bottom: 1px solid #e8e8e8;
                margin-bottom: 16px;
            }
            .bottom {
                font-size: 16px;
                color: #919191;
                @include flexCenter(row);
                justify-content: space-between;
            }
            .rate {
                span {
                    font-size: 14px;
                }
            }
            i {
                color: $dark-orange;
            }
            .discount-price {
                margin-right: 14px;
                text-decoration: line-through;
            }
            .real-price {
                color: $black;
            }
        }
    }
    .more-btn {
        text-align: center;
    }
}

@include max-tablet {
    .popular-courses {
        .nav {
            margin-bottom: 60px;
        }
        .nav-link {
            font-size: 13px;
        }
        .nav-item {
            line-height: 60px;
        }
    }
}

@include max-mobile {
    .popular-courses {
        .nav {
            margin-bottom: 30px;
        }
        .nav-link {
            font-size: 15px;
            padding: 0px 5px;
        }
        .nav-item {
            line-height: 42px;
        }
        .image {
            a {
                display: block;
            }
            img {
                width: 100%;
                object-fit: cover;
            }
        }
    }
}
