.blog-area-detail {
    .left {
        p {
            color: $medium-grey;
            margin-bottom: 20px;
            font-size: 15px;
        }
        .post__header {
            .image {
                margin-bottom: 26px;
            }
            .title {
                font-size: 24px;
                margin-bottom: 15px;
                color: $purple;
            }
        }
        .post__detail {
            border-bottom: 1px solid $grey-border;
            margin-bottom: 15px;
            .quote-wrapper {
                padding: 30px;
                background: rgba(130, 139, 178, 0.1);
                line-height: 1.733;
            }
            .quote {
                padding: 25px 25px 25px 30px;
                background-color: white;
                border-left: 2px solid;
                p {
                    font-style: italic;
                    margin-bottom: 0px;
                }
            }
        }
        .post__bottom {
            @include flexCenter(row);
            justify-content: space-between;
            color: $medium-grey;
            font-size: 14px;
            margin-bottom: 55px;
            .like-info {
                i {
                    margin-right: 5px;
                }
            }
            .list-societies {
                @include flexCenter(row);
                .society:not(:last-child) {
                    margin-right: 15px;
                }
                a {
                    color: $medium-grey;
                }
            }
        }
        .navigation {
            padding-bottom: 30px;
            margin-bottom: 50px;
            border-bottom: 1px solid #eee;
            .item {
                @include flexCenter(row);
                justify-content: stretch;
                margin-bottom: 0px;
                &:hover {
                    .icon {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }
            .icon {
                font-size: 19px;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                background-color: rgba(0, 0, 0, 0.5);
                color: white;
                opacity: 0;
                visibility: hidden;
                @include flexCenter(row);
            }
            .image {
                position: relative;
                margin-right: 20px;
            }
            .info {
                @include flexCenter(column);
                align-items: stretch;
            }
            p {
                font-size: 16px;
                margin-bottom: 0px;
            }
            h4 {
                font-size: 18px;
                line-height: 25px;
            }
            .next-item {
                justify-content: flex-end;
                .image {
                    margin-right: 0px;
                    margin-left: 20px;
                }
                .info {
                    text-align: right;
                }
            }
        }
        .author {
            @include flexCenter(row);
            padding: 40px 30px;
            margin-bottom: 50px;
            border-bottom: 1px solid #eee;
            align-items: stretch;
            background: $normal-purple;
            .image {
                flex: 0 0 90px;
                height: 90px;
                margin-right: 30px;
                img {
                    height: 100%;
                }
            }
            .info {
                @include flexCenter(column);
                align-items: flex-start;
                .name {
                    font-size: $font-size-secondary;
                    margin-bottom: 8px;
                }
            }
        }

        /* */
        .comment-section,
        .comment-form {
            .title {
                font-size: 18px;
                margin-bottom: 35px;
                font-weight: 500;
            }
        }

        /* */
        .comment-section {
            padding-bottom: 45px;
            border-bottom: 1px solid #eee;
            .comment {
                @include flexCenter(row);
                align-items: stretch;
                margin-bottom: 48px;
            }
            .image {
                flex: 0 0 70px;
                margin-right: 20px;
            }
            .info {
                .desc {
                    font-size: 15px;
                    margin-bottom: 10px;
                }
            }
            .info__bottom {
                @include flexCenter(row);
                justify-content: space-between;
                .date {
                    font-size: 14px;
                    margin-left: 20px;
                    margin-right: auto;
                    color: #999999;
                }
                h4 {
                    color: #666666;
                    font-size: $font-size-secondary;
                }
                .btn-reply {
                    padding: 5px 18px;
                    font-size: 14px;
                }
            }
        }

        .comment-form {
            padding-top: 45px;
            margin-bottom: 30px;
            input,
            textarea {
                margin-bottom: 30px;
            }
            input {
                height: 48px;
            }
            textarea {
                height: auto;
            }
            .btn {
                width: auto;
                height: 59px;
            }
        }
    }
}

@include max-mobile {
    .blog-area-detail {
        .left {
            .post__header {
                .title {
                    font-size: 18px;
                }
            }

            .author {
                padding: 20px 8px;
                .image {
                    flex: 0 0 45px;
                    height: 45px;
                    margin-right: 15px;
                    img {
                        height: 100%;
                    }
                }
            }

            .navigation {
                .item {
                    &:first-child {
                        margin-bottom: 30px;
                    }
                }
            }

            .post__detail {
                .quote-wrapper {
                    padding: 15px;
                }
                .quote {
                    padding: 15px 15px 15px 20px;
                }
            }
            .author {
                margin-bottom: 20px;
                .desc {
                    line-height: 23px;
                }
            }

            .comment-section {
                .comment {
                    flex-wrap: wrap;
                    text-align: center;
                    .image {
                        flex: 0 0 100%;
                    }
                    .info {
                        text-align: left;
                    }
                    .date {
                        line-height: 20px;
                    }
                }
            }
        }
    }
}

@include max-smobile {
    .blog-area-detail {
        .left {
            .comment-form {
                .btn {
                    width: 100%;
                }
            }
        }
    }
}
