.large-banner-courses{
    background-image: url('../assets/images/bradcam2.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 286px 0px 186px 0px;
    color: white;
    margin-bottom: 100px;
    i{
        color: $dark-orange;
        font-size: 17px;
    }
    .title{
        font-size: 46px;
        line-height: 56px;
        font-weight: 400;
        margin-bottom: 8px;
    }
    .price{
        line-height: 43px;
        margin-bottom: 14px;
        span{
            font-family: "Poppins", sans-serif;
            font-size: 30px;
            font-weight: 400;
        }
        .discount{
            margin-right: 54px;
            text-decoration: line-through;
        }
        .real-price{
            color: $cyan;
        }
    }
    .rating{
        margin-bottom: 15px;
        letter-spacing: 2px;
        span{
            font-size: 14px;
            font-weight: 400;
        }
    }

    .video-hours{
        @include flexCenter(column);
        align-items: flex-start;
        i{
            font-size: 15px;
            color: #AD9EEB;
            margin-right: 14px;
        }
        span{
            font-family: "Poppins", sans-serif;
            font-size: 18px;
        }
    }
}

@include max-mobile{
    .large-banner-courses{
        padding-top: 150px;
        padding-bottom: 150px;
        margin-bottom: 50px;
        .title{
            font-size: 30px;
            line-height: 36px;
        }
        .price{
            line-height: 29px;
            margin-bottom: 14px;
            span{
                font-size: 20px;
            }
            .discount{
                font-size: 20px;
            }
        }
        .rating{
            i{
                font-size: 16px;
            }
        }
    }
}