/* === BEGIN COMMON SCSS === */
* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

body {
    position: relative;
    font-family: "Crimson Text", serif;
    font-size: $font-size-primary;
    line-height: $line-height-primary;
    font-weight: 400;
    color: $black;
    &.overflow-hidden {
        overflow: hidden;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Poppins", sans-serif;
    margin-bottom: 0;
}

ol {
    list-style-type: decimal-leading-zero;
}

ul {
    list-style-type: none;
    margin-bottom: 0;
}

img {
    width: auto;
    max-width: 100%;
}

span {
    display: inline-block;
}

p {
    margin-bottom: 0px;
}

::placeholder {
    /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: $light-grey;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: $light-grey;
}

::-ms-input-placeholder {
    /* Microsoft Edge */
    color: $light-grey;
}

a {
    display: inline-block;
    color: white;
    text-decoration: none !important;
    @include transition-ease-faster;
    &:hover {
        color: white;
    }
    &:focus {
        color: white;
    }
    &.orange {
        &:hover {
            color: $orange !important;
        }
    }
    &.cyan {
        &:hover {
            color: $cyan !important;
        }
    }
    &.white {
        &:hover {
            color: white !important;
        }
    }
    &.purple {
        &:hover {
            color: $dark-purple !important;
        }
    }
    &.default {
        color: $medium-purple;
        background: #f9f9ff;
        border: 1px solid transparent;
        &:hover {
            color: $medium-purple !important;
        }
    }
    &.underline {
        color: $medium-purple;
        text-decoration: underline !important;
        &:hover {
            color: $medium-purple !important;
        }
    }
    &.disabled {
        color: black;
        background: #f9f9ff;
        cursor: not-allowed;
        &:hover {
            color: $medium-purple !important;
        }
    }
}

.btn {
    border: none;
    color: white;
    border-radius: 30px;
    padding: 17px 50px;
    &:focus {
        outline: 0;
        box-shadow: none;
    }
    &.b-cyan {
        background-color: $cyan;
        border: 1px solid $cyan;
        &:hover {
            color: $cyan;
            background-color: transparent;
        }
    }
    &.b-cyan-reverse {
        color: $cyan;
        background-color: transparent;
        border: 1px solid $cyan;
        &:hover {
            color: white !important;
            background-color: $cyan;
        }
    }
    &.b-transparent {
        background-color: transparent;
        border: 1px solid $cyan;
        color: $cyan;
        &:hover {
            color: white;
            background-color: $cyan;
        }
    }
    &.b-orange {
        background-color: $orange;
        border: 1px solid $orange;
        &:hover {
            color: $orange;
            background-color: transparent;
        }
    }
    &.b-orange-elements {
        background-color: $orange-element;
        border: 1px solid $orange-element;
        &:hover {
            color: $orange-element;
            background-color: transparent;
        }
    }
    &.b-orange-elements-reverse {
        color: $orange-element;
        background-color: transparent;
        border: 1px solid $orange-element;
        &:hover {
            color: white;
            background-color: $orange-element;
            border: 1px solid $orange-element;
        }
    }

    &.b-blue {
        background-color: $blue;
        border: 1px solid $blue;
        &:hover {
            color: $blue;
            background-color: transparent;
        }
    }
    &.b-blue-reverse {
        color: $blue;
        background-color: transparent;
        border: 1px solid $blue;
        &:hover {
            color: white;
            background-color: $blue;
            border: 1px solid $blue;
        }
    }

    &.b-yellow {
        background-color: $yellow;
        border: 1px solid $yellow;
        &:hover {
            color: $yellow;
            background-color: transparent;
        }
    }
    &.b-yellow-reverse {
        color: $yellow;
        background-color: transparent;
        border: 1px solid $yellow;
        &:hover {
            color: white;
            background-color: $yellow;
            border: 1px solid $yellow;
        }
    }

    &.b-red {
        background-color: $red;
        border: 1px solid $red;
        &:hover {
            color: $red;
            background-color: transparent;
        }
    }
    &.b-red-reverse {
        color: $red;
        background-color: transparent;
        border: 1px solid $red;
        &:hover {
            color: white;
            background-color: $red;
            border: 1px solid $red;
        }
    }
    &.b-light-blue {
        color: white;
        background-color: $light-blue;
        border: 1px solid $light-blue;
        &:hover {
            color: $light-blue;
            background-color: transparent;
            border: 1px solid $light-blue;
        }
    }
    &.b-light-blue-reverse {
        color: $light-blue;
        background-color: transparent;
        border: 1px solid $light-blue;
        &:hover {
            color: white;
            background-color: $light-blue;
            border: 1px solid $light-blue;
        }
    }
    &.border-radius-3 {
        border-radius: 3px !important;
    }
    &.border-radius-20 {
        border-radius: 20px !important;
    }
    &.lh-50 {
        line-height: 50px !important;
    }
    &.lh-45 {
        line-height: 45px !important;
    }
    &.lh-40 {
        line-height: 40px !important;
    }
    &.lh-30 {
        line-height: 30px !important;
    }
    &.lh-25 {
        line-height: 25px !important;
    }
}

.container {
    @include smobile {
        max-width: 540px;
    }
    @include mobile {
        max-width: 720px;
    }
    @include tablet {
        max-width: 960px;
    }
    @include desktop {
        max-width: 1140px;
    }
}

.section-title {
    text-align: center;
    margin-bottom: 100px;
    .title {
        font-size: $font-size-other;
        line-height: 52px;
        font-weight: 400;
        margin-bottom: 23px;
        color: #1f1f1f;
    }
    .desc {
        width: 100%;
        max-width: 390px;
        margin: auto;
        color: $grey;
    }
}

.tf-scale {
    border-radius: 5px;
    overflow: hidden;
    &:hover img {
        transform: scale(1.1);
    }
    .image {
        overflow: hidden;
        img {
            @include transition-ease-faster;
        }
    }
}

@include max-mobile {
    .section-title {
        text-align: center;
        margin-bottom: 40px;
        .title {
            font-size: 23px;
            line-height: 30px;
        }
    }
}
