.newsletter{
    background-color: $cyan;
    padding: 100px 0px;
    color: white;
    margin-bottom: 200px;
    .left{
        margin-bottom: 13px;
    }
    .title{
        font-size: 36px;
        font-weight: 400;
        margin-bottom: 8px;
    }
    h4{
        font-size: $font-size-primary;
        font-weight: 400;
        margin-bottom: 20px;
    }
    form{
        height: 60px;
        @include flexCenter(row);
        justify-content: space-between;
        input, button{
            height: 100%;
            outline: none;
            border: none;
        }
        input{
            padding: 30px;
            width: 70%;
            border-radius: 3px 0px 0px 3px;
        }
        button{
            width: 30%;
            color: white;
            background-color: $dark-orange;
            border-radius: 0px 3px 3px 0px;
        }
    }
}

@include max-mobile{
    .newsletter{
        margin-bottom: 100px;
    }
}