.elements-area {
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: $purple;
    }
    .title {
        margin-bottom: 30px;
        font-size: 24px;
        color: $purple;
    }
    p {
        font-family: "Crimson Text", serif;
        color: $medium-grey;
        margin-bottom: 13px;
    }

    /* */
    .text-sample {
        margin-bottom: 70px;
        b,
        sup,
        sub,
        u,
        del {
            color: $orange-element;
        }
    }

    /* */
    .sample-button {
        margin-bottom: 70px;
        border-top: 1px dotted #eee;
        .button-group {
            margin-bottom: 10px;
        }
        a {
            line-height: 40px;
            padding: 0 30px;
            font-size: 13px;
            text-align: center;
            font-weight: 500;
            margin: 10px 10px 0px 0px;
        }
        i {
            position: relative;
            margin-left: 10px;
            top: 2px;
        }
        .btn {
            border-radius: 0px;
        }
    }

    /* */
    .wrapper {
        > div {
            margin-bottom: 70px;
            border-top: 1px dotted #eee;
        }
    }

    /* */
    .align {
        .left-aligned {
            margin-bottom: 100px;
        }
        .right-info {
            .title {
                text-align: right;
            }
        }
    }

    /* */
    .definition {
        h4 {
            font-size: 18px;
            margin-bottom: 8px;
        }
    }

    /* */
    .block-quotes {
        .form-switches {
            padding: 30px 50px 30px 30px;
            background: #f9f9ff;
            border-left: 2px solid #ff5e13;
            margin-bottom: 16px;
        }
        p {
            color: black;
        }
    }

    /* */
    .table {
        .progress-table-wrap {
            background-color: #f9f9ff;
            overflow-x: auto;
        }
        .progress-table {
            min-width: 800px;
            padding: 15px 0px 30px 0px;
        }
        .table-row,
        .table-head {
            @include flexCenter(row);
            justify-content: stretch;
            background-color: #f9f9ff;
        }
        .table-head {
            line-height: 40px;
            color: #415094;
        }
        .table-row {
            padding: 15px 0;
            border-top: 1px solid #edf3fd;
            img {
                margin-right: 15px;
            }
        }
        .percentage {
            width: 40.36%;
            padding-right: 50px;
        }
        .serial {
            width: 11.83%;
            padding-left: 30px;
        }
        .country {
            width: 28.07%;
        }
        .visit {
            width: 19.74%;
        }
        .progress {
            width: 80%;
            height: 5px;
            border-radius: 0px;
            background-color: transparent;
            .progress-bar {
                width: 100%;
                &.blue {
                    background-color: #6382e6;
                }
                &.pink {
                    background-color: #e66686;
                }
                &.orange {
                    background-color: #f09359;
                }
                &.cyan {
                    background-color: #73fbaf;
                }
                &.purple {
                    background-color: #a367e7;
                }
            }
        }
    }

    /* */
    .image-gallery {
        a {
            display: block;
        }
        .image {
            margin-top: 30px;
            display: block;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            height: 200px;
            background-image: url("../assets/images/gallery/1.jpg");
        }
    }

    /* */
    .list {
        .title {
            margin-bottom: 8px;
        }
        .typography {
            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
                color: #828bb2;
                margin-bottom: 8px;
            }
            h1 {
                font-size: 36px;
            }
            h2 {
                font-size: 30px;
            }
            h3 {
                font-size: 24px;
            }
            h4 {
                font-size: 18px;
            }
            h5 {
                font-size: 16px;
            }
            h6 {
                font-size: 14px;
                font-weight: 400;
            }
        }
        .unordered-list {
            li {
                position: relative;
                padding-left: 30px;
                line-height: 1.82em;
                &::before {
                    position: absolute;
                    content: "";
                    width: 14px;
                    height: 14px;
                    border: 3px solid #ff5e13;
                    top: 4px;
                    left: 0px;
                    border-radius: 50%;
                    background-color: white;
                }
            }
        }
        .ordered-list {
            padding-left: 40px;
            li {
                color: $orange-element;
                margin-left: 30px;
                span {
                    color: #828bb2;
                }
            }
        }
    }

    /* Nice Select */
    .nice-select {
        font-size: 14px;
        width: 100%;
        padding: 0px 20px;
        line-height: 40px;
        background: #f9f9ff;
        outline: none;
        border: none;
        color: black;
    }
    ::placeholder {
        /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: $light-grey;
        opacity: 1; /* Firefox */
    }

    :-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: $light-grey;
    }

    ::-ms-input-placeholder {
        /* Microsoft Edge */
        color: $light-grey;
    }

    /* */
    .wrapper {
        form {
            > input,
            > div,
            > textarea {
                margin-top: 10px;
            }
            textarea {
                height: 100px;
            }
            input,
            textarea {
                font-size: 14px;
                width: 100%;
                padding: 0px 20px;
                line-height: 40px;
                background: #f9f9ff;
                outline: none;
                border: none;
                color: #797979;
            }
        }

        .address,
        .country,
        .city {
            @include flexCenter(row);
            position: relative;
            .nice-select,
            input {
                padding: 0px 40px 0px 45px;
                line-height: 40px;
                height: 40px;
                .list {
                    font-size: 12px;
                    line-height: 28px;
                }
            }
            .icon {
                font-size: 14px;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 15px;
                z-index: 3;
                color: $medium-grey;
            }
        }

        .city {
            .icon {
                background: #f9f9ff;
                z-index: 6;
            }
            i {
                transform: rotate(320deg);
            }
            .nice-select {
                z-index: 5;
            }
        }

        /* */
        .switches {
            margin-bottom: 30px;

            p {
                margin-bottom: 0px;
            }

            select {
                position: relative;
            }

            .select-box {
                display: flex;
                .nice-select {
                    width: auto;
                    .current {
                        margin-right: 50px;
                    }
                }
            }

            .switch {
                font-family: "Crimson Text", serif;
                @include flexCenter(row);
                justify-content: stretch;
                font-size: $font-size-secondary;
                color: $medium-grey;
                margin-bottom: 23px;
                input,
                label {
                    cursor: pointer;
                    margin-bottom: 0;
                }
                .checkbox {
                    width: 35px;
                    height: 17px;
                }
                input {
                    position: absolute;
                    left: 0;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    width: 100%;
                    height: 100%;
                    opacity: 0;
                }
            }

            .primary-checkbox,
            .confirm-checkbox,
            .disabled-checkbox,
            .primary-radio,
            .confirm-radio,
            .disabled-radio {
                width: 16px;
                height: 16px;
                border-radius: 3px;
                background: #f9f9ff;
                position: relative;
                cursor: pointer;
                input + label {
                    position: absolute;
                    left: 0;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    width: 100%;
                    height: 100%;
                    border-radius: 3px;
                }
            }

            .primary-checkbox {
                input:checked + label {
                    background-image: url("../assets/images/check/primary-check.png");
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center center;
                }
            }

            .confirm-checkbox {
                input:checked + label {
                    background-image: url("../assets/images/check/success-check.png");
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center center;
                }
            }

            .disabled-checkbox {
                label {
                    cursor: not-allowed !important;
                }
                input:checked + label {
                    background-image: url("../assets/images/check/disabled-check.png");
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center center;
                }
            }

            .primary-radio {
                input:checked + label {
                    background-image: url("../assets/images/check/primary-radio.png");
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center center;
                }
            }

            .confirm-radio {
                input:checked + label {
                    background-image: url("../assets/images/check/success-radio.png");
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center center;
                }
            }

            .disabled-radio {
                label {
                    cursor: not-allowed !important;
                }

                input:checked + label {
                    background-image: url("../assets/images/check/disabled-radio.png");
                    background-repeat: no-repeat;
                    background-size: cover;
                    background-position: center center;
                }
            }

            .checkbox {
                label {
                    &::before {
                        position: absolute;
                        content: "";
                        left: 0;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        width: 100%;
                        height: 100%;
                        border-radius: 8.5px;
                        background-color: transparent;
                        @include transition-ease-faster;
                    }
                    &::after {
                        position: absolute;
                        content: "";
                        left: 1px;
                        top: 1px;
                        width: 15px;
                        height: 15px;
                        border-radius: 50%;
                        box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.2);
                        background-color: transparent;
                        @include transition-ease-faster;
                    }
                }
            }

            #primary-switch,
            #default-switch {
                &:checked {
                    + label {
                        position: absolute;
                        left: 0;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        width: 100%;
                        height: 100%;
                        &::before {
                            background-color: $orange-element;
                        }
                        &::after {
                            background-color: white;
                            left: 19px;
                        }
                    }
                }
            }

            #confirm-switch {
                &:checked {
                    + label {
                        position: absolute;
                        left: 0;
                        top: 0;
                        right: 0;
                        bottom: 0;
                        width: 100%;
                        height: 100%;
                        &::before {
                            background-color: $light-blue;
                        }
                        &::after {
                            background-color: white;
                            left: 19px;
                        }
                    }
                }
            }
        }
    }
}

.nice-select {
    z-index: 2;
    .list {
        width: 100%;
        border-radius: 0px;
        padding: 0px;
        margin-top: 2px;
        box-shadow: none;
        padding: 10px 0 10px 0px;
        color: black;
    }
    .option.selected {
        color: $orange-element;
    }
}

@include max-tablet {
    .elements-area {
        .align {
            .image {
                text-align: center;
                margin-bottom: 30px;
            }
            .left-aligned {
                margin-bottom: 50px;
            }
        }
        .list,
        .form-switches {
            [class*="col-"] {
                margin-bottom: 30px;
            }
        }
    }
}

@include max-mobile {
    .elements-area {
        /* */
        .wrapper {
            > div {
                margin-bottom: 40px;
                border-top: 1px dotted #eee;
            }
        }
    }
}
