.large-banner{
    @include flexCenter(row);
    background-image: url('../assets/images/large-banner/banner.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    margin-bottom: 200px;
    .right{
        margin-top: 10px;
        color: white;
        .title{
            font-size: 60px;
            line-height: 70px;   
            font-weight: 400;
            margin-bottom: 50px;
        }
    }
}

@include max-desktop{
    .large-banner{
        .right{
            .title{
                font-size: 35px;
                line-height: 45px;   
            }
        }
    }
}

@include max-mobile{
    .large-banner{
        margin-bottom: 100px;
        .right{
            .title{
                font-size: 35px;
                line-height: 42px;   
                margin-bottom: 20px;
            }   
            .browse-btn{
                .btn{
                    padding: 10px 18px;
                }
            }
        }
    }
}


@include max-smobile{
    .large-banner{
        margin-bottom: 100px;
        .right{
            .title{
                font-size: 30px;
                line-height: 40px;   
                text-align: center;
                margin-bottom: 20px;
            }
            .browse-btn{
                text-align: center;
                .btn{
                    padding: 10px 20px;
                }
            }
        }
    }
}