.overlay{
    position: fixed;
    background-color: rgba(46, 0, 75,0.8);
    opacity: 0.8;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 20;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
    @include transition-ease-faster;
    &.open{
        opacity: 1;
        visibility: visible;      
    }
}

header{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    background-color: transparent;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    z-index: 10;
    &.open{
        position: fixed;
        top: 0px;
        left: 0px;
        right: 0px;
        animation: mymove .5s ease;
        background: linear-gradient(to right, #a64eee 0%, #3c35ce 100%);
        box-shadow: 0px 3px 16px 0px rgba(0, 0, 0, 0.1);
        @keyframes mymove {
            0% {
                transform: translateY(-100%);
            }
            100% {
                transform: translateY(0%);
            }
        }
    }
    .main-header-area{
        padding: 0px 10px;
    }
    .list-items, .item, .log-chat-area, .log-in__button, .logo{
        @include flexCenter(row);
    }
    .nav{
        justify-content: center;
        .item{
            position: relative;
            font-family: "Poppins", sans-serif;
            line-height: 90px;
            margin-right: 19px;
            font-size: 15px;
            i{
                font-size: 10px;
            }
            .list__s-items{
                position: absolute;
                left: 0;
                top: 140%;
                text-align: left;
                width: 200px;            
                background-color: white;
                color: $black;
                opacity: 0;
                visibility: hidden;
                @include transition-ease;
                box-shadow: 0 0 10px rgba(0, 0, 0, 0.02);
                .s-item{
                    line-height: 28px;
                    margin-right: 15px;
                }
                a{
                    padding: 10px 15px;
                    color: $black; 
                }
            }
            &::before{
                content: '';
                position: absolute;
                bottom: -1px;
                left: 0;
                right: 0;
                background-color: $cyan;
                height: 3px;
                opacity: 0;
                visibility: hidden;
                transform: scaleX(0);
                @include transition-ease-faster;
            }
            &.active{
                &::before{
                    content: '';
                    position: absolute;
                    bottom: -1px;
                    left: 0;
                    right: 0;
                    background-color: $cyan;
                    height: 3px;
                    opacity: 1;
                    visibility: visible;
                    transform: scaleX(1);
                }
            }
            &:hover{
                &::before{
                    opacity: 1;
                    visibility: visible;
                    transform: scaleX(1);
                }
            }
            &:hover .list__s-items{
                opacity: 1;
                visibility: visible;   
                top: 100%;
            }
        }
    }

    .log-chat-area{
        justify-content: space-between;
        color: white;
        span{
            font-size: $font-size-secondary;
        }
        .log-in__button{
            margin: 0px 50px 0px auto;
            i{
                font-size: 15px;
                margin-right: 12px;
            }
        }
        .btn{
            font-size: $font-size-secondary;
            background-color: $orange;
            padding: 12px 25px;
            border-radius: 30px;
            color: white;
            font-size: 13px;
            i{
                font-size: 10px;
                margin-right: 7px;
            }
        }
    }
    .icon-burger, .nav-mobile{
        display: none;
    }
    
}

@include max-desktop{
    header{
        .main-header-area{
            padding: 0px 20px;
        }
        .log-chat-area{
            .log-in__button{
                margin: 0px 7px 0px auto;
            }
        }
    }
}

@include max-tablet{
    header{
        .main-header-area{
            height: 59px;
            @include flexCenter(row);
        }
        .icon-burger{
            display: block;
            text-align: right;
            font-size: 30px;
            color: white;
        }
        .nav{
            display: none;
        }
        .nav-mobile{
            position: absolute;
            width: 100%;   
            background-color: white;
            top: calc(100% + 12px);
            .list-items__mobile{
                @include flexCenter(column);
                align-items: flex-start;
                i{
                    font-size: 12px;
                }
                .item__mobile{
                    @include flexCenter(row);
                    flex-wrap: wrap;
                    width: 100%;
                    justify-content: space-between;
                    a{
                        color: black;
                        font-size: 14px;
                    }
                    > a{
                        @include flexCenter(row);
                        width: 100%;
                        justify-content: space-between;
                        padding: 5px 10px;
                    }
                }
            }
            .s-items__mobile{
                display: none;
                width: 100%;
                top: 100%;
                margin: 0px 0px 0px 20px;
                a{
                    width: 100%;
                    padding: 5px 10px;
                    margin: 2px 5px;
                }
            }
        }
        .log-chat-area{
            .log-in__button, .btn{
                display: none;
            }
        }
        .logo{
            justify-content: flex-start;
        }
    }
}