.our-course{
    margin-bottom: 100px;
    .list-specialities{
        @include flexCenter(row);
    }
    .speciality{
        text-align: center;
        border: 1px solid #E8E8E8;
        padding: 38px 30px;
        @include transition-ease-faster;
        i, .title, p{
            @include transition-ease-faster;
        }
        &{
            margin-bottom: 30px;
        }
        &:hover{
            background-color: $dark-orange;
            i, .title, p{
                color: white;
            }
        }
        .icon{
            font-size: 45px;
            height: 74px;
            &.orange{
                color: $dark-orange;
            }
            &.cyan{
                color: $cyan;
            }
            &.light-purple{
                color: $light-purple;
            }
            margin-bottom: 7px;
            
        }
        .title{
            font-size: 18px;
            font-weight: 400;
            margin-bottom: 22px;
        }
        p{
            color: $grey;
            margin-bottom: 13px;
        }
    }
}

@include max-mobile{
    .our-course{
        margin-bottom: 20px;
        .speciality{
            .icon{
                font-size: 38px;
                height: 60px;
            }
        }
    }
}