footer{
    background-image: url('../assets/images/ft-banner/footer_bg.webp');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin-top: 120px;
    a, p, i{
        color: $light-grey;
        font-size: $font-size-secondary;
    }
    .top{
        padding-top: 145px;
        padding-bottom: 129px;
        [class*="col-"]{ 
            margin-bottom: 30px;
        }
        .title{
            display: flex;
            .icon{
                margin-left: auto;
            }
        }
        .desc{
            margin-bottom: 47px;
        }
        .icon{
            opacity: 0;
            visibility: hidden;
            i{
                color: white;
                font-size: 14px;
            }
            margin-left: auto;
        }
        .list-socities{
            @include flexCenter(row);
            justify-content: flex-start;
            .society{
                @include flexCenter(row);
                width: 50px;
                height: 50px;
                background-color: rgb(133, 101, 226);
                border-radius: 3px;
                margin-right: 10px;
                a{
                    text-align: center;
                    width: 100%;
                    line-height: 50px;
                    i{
                        @include transition-ease-faster;
                    }
                    &:hover i{
                        color: white;
                    }
                }
            }
        }
        .list-items__ft{
            a{
                line-height: 42px;
            }
        }
        .info-area{
            width: 100%;
            max-width: 195px;
        }
        .ft-logo, .title{
            font-size: 22px;
            font-weight: 400;
            margin-bottom: 40px;
            color: white;
        }
    }
    .bottom{
        border-top: 1px solid rgba(255, 255, 255, 0.2);
        text-align: center;
        p{
            padding: 30px 0px;
            a{
                color: $orange;
            }
        }
    }
}

@include max-mobile{
    footer{
        margin-top: 0px;
        .top{
            padding-top: 60px;
            padding-bottom: 30px;
            [class*="col-"]{ 
                &:not(:first-child):not(:last-child){
                    .title{
                        margin-bottom: 10px;
                    }
                    margin-bottom: 15px;
                }
            }
            .ft-logo, .title{
                margin-bottom: 20px;
            }
            .title{
                font-size: 18px;
            }
            .icon{
                opacity: 1;
                visibility: visible;
            }
            .list-items__ft{
                max-height: 0px;
                overflow: hidden;
                @include transition-ease-faster;
                &.open{
                    max-height: 100%;   
                }
            }
        }
    }
}