.our-blog {
    margin-bottom: 170px;
    .cyan {
        color: $cyan;
    }
    .blog {
        box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
        overflow: hidden;
        .image {
            margin-bottom: 35px;
        }
        .info {
            padding: 4px 15px 30px;
        }
        .date {
            font-size: 14px;
        }
        .title {
            a {
                font-size: 18px;
                color: $black;
                line-height: 21px;
            }
        }
        .desc {
            color: $grey;
        }
    }
}

@include max-mobile {
    .our-blog {
        margin-bottom: 10px;
        .list-blogs {
            [class*="col-"] {
                margin-bottom: 30px;
            }
        }
        .blog {
            .image {
                margin-bottom: 35px;
                a {
                    display: block;
                }
                img {
                    width: 100%;
                    object-fit: cover;
                }
            }
        }
    }
}
