.blog-area {
    /* */
    a {
        color: $black;
    }
    h3 {
        color: $purple;
        a {
            color: $purple;
        }
    }

    /* */
    form {
        input,
        .btn,
        button,
        textarea {
            @include flexCenter(row);
            width: 100%;
            height: 50px;
            border: 1px solid #f0e9ff;
            color: #999999;
            outline: none;
        }
        .btn {
            font-family: "Poppins", sans-serif;
            font-size: 14px;
            color: $cyan;
            border: 1px solid $cyan;
            border-radius: 0px;
            letter-spacing: 3px;
        }
        input,
        textarea {
            font-size: 13px;
            padding: 6px 12px 6px 20px;
        }
        ::placeholder {
            /* Chrome, Firefox, Opera, Safari 10.1+ */
            color: #999999;
            opacity: 1; /* Firefox */
        }

        :-ms-input-placeholder {
            /* Internet Explorer 10-11 */
            color: #999999;
        }

        ::-ms-input-placeholder {
            /* Microsoft Edge */
            color: #999999;
        }
        .form__header {
            @include flexCenter(row);
            justify-content: stretch;

            ::placeholder {
                /* Chrome, Firefox, Opera, Safari 10.1+ */
                color: #999999;
                opacity: 1; /* Firefox */
            }

            :-ms-input-placeholder {
                /* Internet Explorer 10-11 */
                color: #999999;
            }

            ::-ms-input-placeholder {
                /* Microsoft Edge */
                color: #999999;
            }
            .icon-search {
                button {
                    padding: 4px 15px;
                    background-color: white;
                    outline: none;
                    border-left: 1px solid white;
                }
            }
        }
    }

    /* */
    .left {
        .title {
            font-weight: 600;
        }
        .item {
            margin-bottom: 50px;
        }
        .list-links {
            @include flexCenter(row);
            justify-content: stretch;
            a {
                color: #666666;
            }
        }
        .item__header {
            position: relative;
        }
        .item__bottom {
            padding: 60px 30px 35px 35px;
        }
        .info {
            .title {
                font-size: 24px;
                margin-bottom: 15px;
            }
            .desc {
                margin-bottom: 30px;
                color: $medium-grey;
            }
        }
        .item_date {
            padding: 12px 29px;
            position: absolute;
            bottom: -20px;
            left: 40px;
            background-color: $purple;
            border: 1px solid transparent;
            border-radius: 5px;
            @include transition-ease;
            &:hover {
                background-color: transparent;
                border: 1px solid $purple;
                h3 {
                    color: $purple;
                }
                p {
                    color: $purple;
                }
            }
            h3 {
                font-size: 30px;
                color: white;
                @include transition-ease;
            }
            p {
                font-size: 18px;
                color: white;
                @include transition-ease;
            }
        }

        .link {
            i {
                font-size: 13px;
                margin-right: 5px;
            }
            a {
                font-size: 14px;
            }
            &:first-child {
                &::after {
                    position: relative;
                    content: "|";
                    padding: 0px 10px;
                }
            }
        }

        .blog-pagination {
            margin-top: 80px;
            .pagination {
                justify-content: center;
            }

            a {
                @include flexCenter(row);
                padding: 0px;
                height: 100%;
                font-size: 14px;
                border-radius: 0px;
                transition-duration: 0.3s;
                border: 1px solid $grey-border;
                color: #666666;
                &:hover {
                    background-color: $normal-purple;
                }
            }

            .page-item {
                margin-right: 10px;
                width: 45px;
                height: 45px;
                border-radius: 0px;
            }
        }
    }

    /* */
    .right {
        .single-sidebar-widget {
            padding: 30px;
            margin-bottom: 30px;
            background-color: $normal-purple;
            .title {
                font-size: 20px;
                margin-bottom: 30px;
                &::after {
                    content: "";
                    display: block;
                    padding-top: 15px;
                    border-bottom: 1px solid #f0e9ff;
                }
            }

            button {
                width: 100%;
            }
        }

        /* */
        .post-category-widget {
            .item {
                border-bottom: 1px solid #f0e9ff;
                transition: all 0.3s ease 0s;
                padding-bottom: 12px;

                a {
                    color: $medium-grey;
                }

                & + .item {
                    padding-top: 15px;
                }
            }
            .post-item {
                @include flexCenter(row);
                justify-content: flex-start;
                margin-bottom: 20px;

                a {
                    color: $purple;
                }

                .image {
                    flex: 0 0 80px;
                    margin-right: 20px;
                    padding-left: 0px;
                }

                .info {
                    @include flexCenter(column);
                    align-items: flex-start;
                    .name {
                        font-size: $font-size-secondary;
                        line-height: 20px;
                        margin-bottom: 6px;
                        color: #666666;
                    }
                    .date {
                        font-size: 14px;
                        line-height: 21px;
                        color: $medium-grey;
                    }
                }
            }
        }

        /* */
        .tag-clouds {
            .list-tags {
                @include flexCenter(row);
                flex-wrap: wrap;
                justify-content: flex-start;
            }

            .tag {
                margin-bottom: 8px;
                margin-right: 3px;
                background-color: white;
            }

            a {
                @include flexCenter(row);
                font-size: 13px;
                padding: 4px 20px;
                border: 1px solid #eeeeee;
                line-height: 1.5;
                color: #666666;
                @include transition-ease;
                &:hover {
                    background-color: #8446e3;
                    color: white !important;
                }
            }
        }

        /* */
        .instagram-feeds {
            .list-image {
                display: flex;
                flex-wrap: wrap;
                margin: 0 -6px;
            }
            .image {
                width: calc(100% / 3);
                padding-right: 6px;
                padding-left: 6px;
                margin-bottom: 12px;
                a,
                img {
                    width: 100%;
                }
            }
        }

        /* */
        .newsletter-widget {
            .form__header {
                @include flexCenter(column);
                align-items: stretch;
            }
        }
    }
}

@include max-tablet {
    .blog-area {
        .left {
            .item__bottom {
                padding: 30px 0 20px 10px;
                box-shadow: 0px 10px 20px 0px rgba(221, 221, 221, 0.3);
            }

            .info {
                .title {
                    font-size: 18px;
                    margin-bottom: 8px;
                }
                .desc {
                    margin-bottom: 30px;
                    color: $medium-grey;
                }
            }

            .item_date {
                bottom: -10px;
                left: 10px;
                padding: 8px 15px;
                h3 {
                    font-size: 22px;
                }
            }

            .blog-pagination {
                margin-top: 0px;
                margin-bottom: 20px;
            }
        }
    }
}
